body, html {
	margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Mulish", sans-serif;
  background: #f2f2f5;
}

.content {
	background-color: #f2f2f5;
	padding: 20px 40px;
	border-radius: 30px;
}

.left, .right{
	width: 400px;
}


form {
	width: 100%;
}

.left, .right {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
}


.file-drop-div {
	background-color: #2F2F43;
	position: relative;
	width: 100%;
	height: 200px;
	padding: 20px;
	border-radius: 30px;
}

.upload-logo {
	margin: 10px 0;
}


.browse-div {
	display: flex;
	justify-content: center;
	align-items: center;
	position: relative;
}


#browse-input{
	width: 200px;
	margin: auto;
	height: 60px;
	outline: none;
	cursor: pointer;
	background-color: rgba(73, 89, 233, 0.2);
	color: #4959E8;

}

#browse-input-label, .btn-tip{
	display: inline-block;
	background-color: #DE5246;
	color: #f2f2f5;
	padding: 0.2rem;
	font-family: sans-serif;
	border-radius: 20px;
	cursor: pointer;
	font-size: 25px;
	width: 200px;
	text-align: center;
  }

  .tip-enter-div {
	  display: flex;
	  justify-content: center;
	  align-items: center;
  }

  .tip-enter-div > input {
	  border-radius: 5px;
  }

  .btn-tip {
	font-size: 16px;
	width: 75px;
  }


.or-div {
	display: flex;
	justify-content: center;
	margin: 20px 0;
}

.or-div>h3{
	margin: 0;
}

.show-files-to-upload {
	margin-top: 20px;
}

.mp3-icon {
	width: 40px;
	height: auto;
	display: inline-block;
}

.file-name {
	display: inline-block;
	margin-left: 20px;
}

.card-main, .list-group-item, #imageList, .list-group-flush {
	background-color: #f2f2f5 !important;
}

.white {
	color: white !important;
}

.rhap_container{
